$babyPowder : #FDFFFC;
$oxfordBlue : #02182B;
$persianGreen : #18A999;

.contact-top{
    position: absolute;
    top: 360vh;
    width: 100%;
    height:300px;
    background-color: $oxfordBlue;
    padding: 30px;
    h2,p{
        color: $babyPowder;
    }
    a{
        text-decoration: none;
        color: $babyPowder;
        &:hover{
            color: $babyPowder;
            text-decoration: none;
        }
    }
}


@media only screen and (max-width:699px){
    .contact-top{
        position: absolute;
        top: 540vh;
        width: 100%;
        height:100%;
        background-color: $oxfordBlue;
        padding: 30px;
     
    }
}