$babyPowder : #FDFFFC;
$oxfordBlue : #02182B;
$persianGreen : #18A999;


.project-top{
    position: absolute;
    top: 180vh;
    margin-bottom: 100px;
}

h1{
    color: $persianGreen;
    font-weight: bold; 
    font-size: 60px;
}

.padding{
    padding-left: 200px;
    text-align: justify;
}

    .project-images{
        width: 125px;
        height: 125px;
        padding: 5px 10px ;
        border-radius: 10px;
        box-shadow: 10px 5px 10px rgba(black, 0.2);
        margin-bottom: 10px;
        margin-left: 50px;
    }

    .project-images:hover{
        box-shadow: 10px 5px 10px rgba(black, 0.5);
    }


    @media only screen and (max-width:699px){
        .project-top{
            position: absolute;
            top: 220vh;
            margin-bottom: 100px;
        }
        h1{
            color: $persianGreen;
            font-weight: bold; 
            font-size: 20px;
        }
        
        .padding{
            padding-left: 0;
            text-align: justify;
        }

        .project-images{
            margin: auto;
            overflow: hidden;
        }
    }